.content-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.main-heading {
  font-size: 100px;
  text-align: left;
  margin-bottom: 2rem;
  font-weight: bold;
}
.button-container {
  display: flex;
  flex-direction: row;
  gap: 3rem;
  justify-content: flex-start;
  align-items: center;
}
@media (max-width: 768px) {
  .content-container {
    align-items: center;
  }
  .main-heading {
    font-size: 48px;
    text-align: center;
  }
  .button-container {
    flex-direction: column;
    gap: 1.5rem;
    align-items: center;
  }
}
.fade-out {
  opacity: 0;
  transition: opacity 0.3s ease-out;
}
.logo-hidden {
  opacity: 0;
  transform: translate(-10px, -10px);
}
.logo-animate-in {
  animation: logoFadeIn 0.6s ease-out forwards;
}
@keyframes logoFadeIn {
  from {
    opacity: 0;
    transform: translate(-10px, -10px);
  }
  to {
    opacity: 1;
    transform: translate(0, 0);
  }
}
